export class OilSalesModel {
	id: number;
	userId: number;
	userName: string;
	category: string;
	categoryId: number;
	price: string;
	amount: string;
	date: string;

	clear() {
		this.id = 0;
		this.userId = 0;
		this.userName = '';
		this.category = '';
		this.price = '';
		this.amount = '';
		this.date = new Date().toISOString().substr(0, 16);
	}

	initialize() {
		this.id = 1;
		this.userId = 1;
		this.userName = 'Z';
		this.category = 'gasoline';
		this.price = '6.5';
		this.amount = '5';
		this.date = new Date().toISOString().substr(0, 16);
	}
}
