// Angular
import { Component, OnInit } from '@angular/core';
import globalVariables from '../globalVariables';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {LayoutConfigService} from '../../../core/_base/layout';
import {Store} from '@ngrx/store';
import {AppState} from '../../../core/reducers';
import {Subscription} from 'rxjs';

@Component({
	selector: 'gas-oil',
	templateUrl: 'oil.component.html',
	styleUrls: ['oil.component.scss'],
})
export class OilComponent implements OnInit {
	employees = [];
	fuelTypes = [];
	gasPumps = [];
	suppliers = [];

	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	role = '';

	constructor(private layoutConfigService: LayoutConfigService, private store: Store<AppState>,
				private http: HttpClient) {
	}

	ngOnInit(): void {
		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.token = state.authToken;
			this.headers = new HttpHeaders({Authorization: 'Bearer ' + this.token, responseType: 'text'});
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);

		this.getEmployees();
		this.getFuelTypes();
		this.getSuppliers();
	}

	/**
	 * On Destroy
	 */
	ngOnDestroy() {
		this.subscriptions.forEach(el => el.unsubscribe());
	}

	/** Actions */
	getFuelTypes() {
		this.http.get(
			globalVariables.baseUrl + 'api/Categories?Type=oil',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.fuelTypes = res.data.map(item => {
				return {
					name: item.categoryName,
					id: item.id
				}
			});
		});
	}

	getSuppliers() {
		this.http.get(
			globalVariables.baseUrl + 'api/Suppliers',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.suppliers = res.data.map(item => {
				return {
					name: item.name,
					id: item.id
				}
			});
		});
	}

	getEmployees() {
		this.http.get(
			globalVariables.baseUrl + 'api/Users',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.employees = res.data.map(item => {
				return {
					name: item.name,
					id: item.userId
				}
			});
		});
	}
}
