export class OilPaymentModel {
	id: number;
	paymentTypeId: number;
	paymentType: string;
	paymentValue: string;
	date: string;

	clear() {
		this.id = 0;
		this.paymentType = '';
		this.paymentValue = '';
		this.date = new Date().toISOString().substr(0, 16);
	}

	initialize() {
		this.id = 1;
		this.paymentType = 'cash';
		this.paymentValue = '2000';
		this.date = new Date().toISOString().substr(0, 16);
	}
}
