// Angular
import {Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
// Material
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
// RxJS
import {Subscription, of} from 'rxjs';
import {delay} from 'rxjs/operators';
// NGRX
import {Update} from '@ngrx/entity';
import {Store, select} from '@ngrx/store';
// State
import {AppState} from '../../../../../core/reducers';
// CRUD
import {LayoutUtilsService, TypesUtilsService} from '../../../../../core/_base/crud';
// Services and Models
import {selectCustomersActionLoading} from '../../../../../core/e-commerce';
import {OilPurchasesModel} from '../models/oil-purchases.model';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import globalVariables from '../../../globalVariables';

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'oil-purchases-edit-dialog',
	templateUrl: 'oil-purchases-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class OilPurchasesEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	oilPurchase: OilPurchasesModel;
	oilPurchaseForm: FormGroup;
	hasFormErrors = false;
	viewLoading = false;
	oilTypes = [];
	oilSuppliers = [];
	currencies = [];
	date = new Date();
	tzoffset = (this.date).getTimezoneOffset() * 60000;
	// Private properties
	private componentSubscriptions: Subscription;
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	role = '';

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<UsersReadingsEditDialogComponent>
	 * @param data: any
	 * @param fb: FormBuilder
	 * @param store: Store<AppState>
	 * @param layoutUtilsService
	 * @param http
	 */
	constructor(public dialogRef: MatDialogRef<OilPurchasesEditDialogComponent>,
				@Inject(MAT_DIALOG_DATA) public data: any,
				private fb: FormBuilder,
				private store: Store<AppState>,
				private layoutUtilsService: LayoutUtilsService,
				private http: HttpClient) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.store.pipe(select(selectCustomersActionLoading)).subscribe(res => this.viewLoading = res);
		this.oilPurchase = this.data.reading;
		this.oilTypes = this.data.oilTypes;
		this.oilSuppliers = this.data.suppliers;
		this.createForm();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.role = localStorage.getItem('role');
			this.token = state.authToken;
			this.headers = new HttpHeaders({Authorization: 'Bearer ' + this.token, responseType: 'text'});
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);
	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}
	}

	createForm() {
		let date = this.oilPurchase.date;
		if (this.oilPurchase.id > 0)
		date = new Date(+new Date(this.oilPurchase.date) - this.tzoffset).toISOString().substr(0, 16);

		this.oilPurchaseForm = this.fb.group({
			id: [this.oilPurchase.id, Validators.required],
			category: [this.oilPurchase.categoryId, Validators.required],
			price: [this.oilPurchase.price, Validators.required],
			amountBought: [this.oilPurchase.amountBought, Validators.required],
			// currency: [this.oilPurchase.currencyId, Validators.required],
			supplierName: [this.oilPurchase.supplierId, Validators.required],
			// paidMoney: [this.oilPurchase.paidMoney, Validators.required],
			date: [date, Validators.required],
		});
	}

	/**
	 * Returns page title
	 */
	getTitle(): string {
		if (this.oilPurchase.id > 0) {
			return `تعديل الشراء للصنف '${this.oilPurchase.category}'`;
		}

		return 'اضافة حركة شراء';
	}

	/**
	 * Check control is invalid
	 * @param controlName: string
	 */
	isControlInvalid(controlName: string): boolean {
		const control = this.oilPurchaseForm.controls[controlName];
		const result = control.invalid && control.touched;
		return result;
	}

	/** ACTIONS */
	getCurrencies() {
		this.http.get(
			globalVariables.baseUrl + 'api/Currencies',
			{headers: this.headers}
		).subscribe((res: any) => {
			this.currencies = res.data.map(item => {
				return {
					name: item.name,
					id: item.id
				};
			});
		});
	}

	/**
	 * Returns prepared oil purchase
	 */
	prepareReading(): OilPurchasesModel {
		const controls = this.oilPurchaseForm.controls;
		const _oilPurchase = new OilPurchasesModel();
		_oilPurchase.id = this.oilPurchase.id;
		_oilPurchase.categoryId = controls.category.value;
		_oilPurchase.price = controls.price.value;
		_oilPurchase.amountBought = controls.amountBought.value;
		// _oilPurchase.currencyId = controls.currency.value;
		// _oilPurchase.paidMoney = controls.paidMoney.value;
		_oilPurchase.supplierId = controls.supplierName.value;
		_oilPurchase.date = controls.date.value;

		return _oilPurchase;
	}

	/**
	 * On Submit
	 */
	onSubmit() {
		this.hasFormErrors = false;
		const controls = this.oilPurchaseForm.controls;
		console.log(this.oilPurchaseForm);

		/** check form */
		if (this.oilPurchaseForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
			return;
		}

		const editedReading = this.prepareReading();
		if (editedReading.id > 0) {
			this.updateReading(editedReading);
		} else {
			this.createReading(editedReading);
		}
	}

	/**
	 * Update Oil price
	 *
	 * @param _oilPurchase: OilPurchasesModel
	 */
	updateReading(_oilPurchase: OilPurchasesModel) {
		this.http.put(
			globalVariables.baseUrl + 'api/Oils/Purchases',
			{
				..._oilPurchase,
				price: +_oilPurchase.price,
				amount: +_oilPurchase.amountBought
			},
			{headers: this.headers}
		)
			.subscribe((res: any) => {
				this.dialogRef.close({_oilPurchase, isEdit: true});
			}, err => {
				let msg = 'فشل في تعديل حركة الشراء';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/**
	 * Create Oil price
	 *
	 * @param _oilPurchase: OilPurchasesModel
	 */
	createReading(_oilPurchase: OilPurchasesModel) {
		let purchaseDate = new Date(+new Date(_oilPurchase.date) - this.tzoffset).toISOString().slice(0, 16);

		this.http.post(
			globalVariables.baseUrl + 'api/Oils/Purchases',
			{
				..._oilPurchase,
				price: +_oilPurchase.price,
				amount: +_oilPurchase.amountBought,
				createdAt: purchaseDate
			},
			{headers: this.headers}
		)
			.subscribe((res: any) => {
				this.dialogRef.close({_oilPurchase, isEdit: false});
			}, err => {
				let msg = 'فشل في اضافة حركة الشراء';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/** Alect Close event */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}
}
