export class OilPurchasesModel {
	id: number;
	category: string;
	categoryId: string;
	amountBought: string;
	price: string;
	currency: string;
	currencyId: number;
	supplierId: number;
	supplierName: string;
	paidMoney: string;
	date: string;

	clear() {
		this.id = 0;
		this.category = '';
		this.amountBought = '';
		this.price = '';
		this.currency = '';
		this.supplierName = '';
		this.paidMoney = '';
		this.date = new Date().toISOString().substr(0, 16);
	}
}
